<template>
  <DxDateBox
    :value="now"
    type="date"
    displayFormat="monthAndYear"
    :calendarOptions="coptions"
  />
</template>

<script>
import DxDateBox from 'devextreme-vue/date-box'
export default {
  name: 'MyMonthSelector',
  components: {
    DxDateBox,
  },
  data() {
    return {
      value: new Date(1981, 3, 27),
      now: new Date(),
      coptions: {
        maxZoomLevel: 'year',
      },
    }
  },
}
</script>

